.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.navbar {
  width: 100%;
  background-color: #555;
  overflow: auto;
  padding: 0px;
}
.navbar a {
  float: left;
  padding: 10px;
  color: white;
  text-decoration: none;
  font-size: 17px;
}
.navbar h3 {
  float:right;
  padding: 10px;
  color: white;
  text-decoration: none;
  font-size: 17px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width:100%;
  margin-top: 50px;
  
}

td, th {
  border: 1px solid #dddddd;
  text-align:center;
  padding: 5px;
}
.apps-list {
  text-align: center;
  margin-top: 50px;
}


.apps-list ul li{
  display: inline-block;
  border: 1px #010514;
  padding: 5px;
  width: 150px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


input[type=text], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}
input[type=password], select, textarea {
  width: 100%;
  padding: 12px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
  margin-top: 6px;
  margin-bottom: 16px;
  resize: vertical;
}

input[type=submit] {
  background-color: #04AA6D;
  color: white;
  padding: 12px 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

input[type=submit]:hover {
  background-color: #45a049;
}

.container {
  width: 30%;
  margin: auto;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.container h1{
  float:none;
  padding: 10px;
  color:#282c34;
  text-decoration: none;
  font-size: 34px;
}

.container a{
  float:inline-start;
  padding: 10px;
  color:#555;
  text-decoration: none;
  font-size: 20px;
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  padding: 50px;
  width: 60%;
  margin:auto;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
}
.grid-item {
  background-color:aliceblue;
  border: 1px solid rgba(0, 0, 0, 0.8);
  padding: 20px;
  font-size: 28px;
  text-align: center;
  text-decoration: none;
  text-decoration-color: #282c34;
  font-family:'Trebuchet MS';
}
.grid-item:hover {
  background-color: rgb(163, 246, 249);
}

.emptab {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 70%;
  margin: auto;
  margin-top: 25px;
}

.emptab td, .emptab th {
  border: 1px solid #ddd;
  padding: 8px;
}

.emptab tr:nth-child(even){background-color: #f2f2f2;}

.emptab tr:hover {background-color: #ddd;}

.emptab th {
  padding-top: 8px;
  padding-bottom: 12px;
  text-align:center;
  background-color: #07149d;
  color: white;
}

.filt{
  position: relative;
  left: 25%;
  top: 5%;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.filt select{
  width: 20%;
  font-size: large;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}